<template>
  <div id="app">
    <hello></hello>
  </div>
</template>

<script>
import Hello from './components/Hello'
    var myhours,myminutes,mytime;
    function getWord(){
      let mydate = new Date();
      myhours = mydate.getHours();
      myminutes = mydate.getMinutes();
      if(myminutes<10){
        myminutes = '0'+myminutes;
      }
      mytime = Number(myhours+'.'+myminutes);
      document.getElementById('round').innerHTML=`<span>${myhours}:${myminutes}</span>`;
      tag();
    }

    function tag() {
      mytime = mytime
      if(mytime<9){
        alert("时间不早了 , 主人明天在打吧 !")
      }else if(mytime<10.3){
        document.getElementsByTagName('h5')[0].setAttribute('class','tag')
      }else if(mytime<11.3){
        document.getElementsByTagName('h5')[1].setAttribute('class','tag')
      }else if(mytime<11.45){
        document.getElementsByTagName('h5')[2].setAttribute('class','tag')
      }else if(mytime<12.3){
        document.getElementsByTagName('h5')[3].setAttribute('class','tag')
      }else if(mytime<13.3){
        document.getElementsByTagName('h5')[4].setAttribute('class','tag')
      }else if(mytime<15){
        document.getElementsByTagName('h5')[5].setAttribute('class','tag')
      }else if(mytime<15.15){
        document.getElementsByTagName('h5')[6].setAttribute('class','tag')
      }else if(mytime<15.3){
        document.getElementsByTagName('h5')[7].setAttribute('class','tag')
      }else if(mytime<16.3){
        document.getElementsByTagName('h5')[8].setAttribute('class','tag')
      }else if(mytime<18){
        document.getElementsByTagName('h5')[9].setAttribute('class','tag')
      }else if(mytime<18.1){
        document.getElementsByTagName('h5')[10].setAttribute('class','tag')
      }else if(mytime<18.4){
        document.getElementsByTagName('h5')[11].setAttribute('class','tag')
      }else if(mytime<19){
        document.getElementsByTagName('h5')[12].setAttribute('class','tag')
      }else if(mytime<19.3){
        document.getElementsByTagName('h5')[13].setAttribute('class','tag')
      }else if(mytime<23){
        document.getElementsByTagName('h5')[14].setAttribute('class','tag')
      }
    }
    window.onload=function(){
      getWord();
      
      var sh = setInterval(function(){
        getWord();
      },10000);
    }

export default {
  name: 'app',
  components: {
    Hello
  }
}
</script>

<style>

body{
  display: flex; width: 100%; height: 100vh; justify-content: center; align-items: center; background: #2570a1;
  text-align: center; 
  font-size: 20px;
}
</style>



